import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Predicciones, RealTimeData, RealData} from './Data'

@Injectable({
  providedIn: 'root'
})
export class DataService {
  constructor(private http: HttpClient){}

  predicciones = './assets/predicciones.json';
  realdata = './assets/bitcoindata.json';
  cryptovalues = "https://api.coinmarketcap.com/v2/ticker/?limit=100&structure=array"
  globaldata = 'https://api.coinmarketcap.com/v2/global/'

  getPredicciones(){
    return this.http.get<Predicciones>(this.predicciones);
  }
  
  getRealData(){
    return this.http.get<RealData>(this.realdata);
  }

  getRealTimeCryptoValues(){
    return this.http.get<RealTimeData>(this.cryptovalues);
  }

  getGlobalData(){
    return this.http.get(this.globaldata);
  }
}
